import  React, { useState } from 'react';
import { Link } from 'react-router-dom';
import logo from "../images/padlock.png"

export const SignUp = () => {
    const [email, setEmail] = useState(null)
    const [password, setPassword] = useState(null)
    const [firstName, setFirstName] = useState()
    const [lastName, setLastName] = useState()
    const [click, setClick] = useState(null)
    const [link, setLink] = useState('')

    const handleFirstNameChange = (e) => {
        const firstName = e.target.value;
        setFirstName(firstName)
    }

    const handleLastNameChange = (e) => {
        const lastName = e.target.value;
        setLastName(lastName)
    }

    const handleEmailChange = (e) => {
        const email = e.target.value;
        setEmail(email)
    }

    const handlePasswordChange = (e) => {
        const password = e.target.value;
        setPassword(password)
    }

    

    const handleChange = (e) => {
        const click = e.target.value;
        setClick(click)
    }
    
    const handleSignUpSubmit = () => {
        let count = parseInt(localStorage.getItem('id')) || 0;
        
        if(click){
        localStorage.setItem('id', count + 1);
        localStorage.setItem('email_' + count, email)
        localStorage.setItem('password_' + count, password)
        localStorage.setItem('firstName_' + count, firstName)
        localStorage.setItem('lastName_' + count, lastName)
        setClick(null)
        setLink('/signIn');
       }
    }
    
    
    return(
        <div className='sign'>
            <div className="logo">
                <img src={logo} alt="logo"/>        
            </div>
            <h1>Sign up</h1>
            <form className="sign_in" action={link}>
                <input name="firstName" className="text-input" type="text" placeholder='First Name *' required onChange={handleFirstNameChange}/>
                <input name="lastName" className="text-input" type="text" placeholder='Last Name *' required onChange={handleLastNameChange}/>
                <input name="email" className="text-input" type="text" placeholder='Email Address *' required onChange={handleEmailChange}/>
                <input name="password" className="text-input" type="text" placeholder='Password *' required onChange={handlePasswordChange}/>
                <p className="check-text"><input name="iWant" className="check" type="checkbox" required onChange={handleChange}/>
                I want to receive inspiration, marketing promotions and updates via email.</p>
                <input className="sign_in_button" type='submit' value="SIGN UP" onClick={handleSignUpSubmit}/>
            </form>
            <p className="link_signup"><Link to="/signIn">Already have an account?Sign in</Link> </p>
            <h6><p className="copyright">Copyright © bodjakaminsyj 2021</p></h6>
                
        </div>
    )
}

