import  React, { useState } from 'react';
import { Link } from 'react-router-dom';
import logo from "../images/padlock.png"


    const id = localStorage.getItem('id')
    let users = {};
    for(let i = 0; i < id; i++){ 
            users['firstName_' + i] = localStorage.getItem('firstName_' + i)
            users['lastName_' + i] = localStorage.getItem('lastName_' + i)
            users['email_' + i] = localStorage.getItem('email_' + i)
            users['password_' + i] = localStorage.getItem('password_' + i)             
        };

    export const SignIn = () => {
    const [email, setEmail] = useState(null);
    const [password, setPassword] = useState(null);
    const [link, setLink] = useState('')
    let name;
    

    const handleEmailChange = (e) => {
        const email = e.target.value;
        setEmail(email)
    }
    const handlePasswordChange = (e) => {
        const password = e.target.value;
        setPassword(password)
        console.log( )
    }

    function handleSignInSubmit(){
       
        for (let i = 0; i < localStorage.length; i++) {
            if(email=== localStorage.getItem('email_' + i)&& password=== localStorage.getItem('password_' + i)){ 
                name = (localStorage.getItem('firstName_' + i)) + ' ' + (localStorage.getItem('lastName_' + i))   
                localStorage.setItem('name', name)
                setLink('/home');
         }       
    }
    
        
                               
            
        
       }
    
    return(
        <div className='sign'>
            <div className="logo">
                <img src={logo} alt="logo"/>
                
            </div>
            <h1>Sign in</h1>
            <form className="sign_in" action={link}>
                <input className="text-input" type="text" placeholder='Email Address *' required onChange={handleEmailChange} />
                <input className="text-input" type="password" placeholder='Password *' required onChange={handlePasswordChange} />
                <p className="remember_me">
                <input type="checkbox" />Remember me
                </p>
                <input className="sign_in_button" type="submit" value="SIGN IN" onClick={handleSignInSubmit}/>
            </form>
            <p className="forgot_password"><Link to="/forgot_password">Forgot password?</Link></p>
            <p className="link_signup"><Link to="/signUp">Don't have an account? Sign Up</Link> </p>
            <h6><p className="copyright">Copyright © bodjakaminsyj 2021</p></h6>
        </div>
    )
}
