import {React} from 'react';
import { Route, Switch } from 'react-router';
import './App.css';
import {appRoutes} from './components/routes/'


function App() {
  return (
    <div className="App">
      <header className="header">
      </header>
      <main>
        <Switch>
      
          {appRoutes.map(route =>(
            <Route key={route.path} {...route}/>
          ))}
         <Route path="*">
        <h1 className="noPage">No found page </h1>
      </Route>
        </Switch>
      </main>
    </div>
  );
}

export default App;
