import { SignUp } from "../pages/signUp";
import { SignIn } from "../pages/signIn"
import { Forgot_Passwort } from "../pages/forgot_password";
import { Home } from "../pages/home";

export const appRoutes = [
    {
        path: '/signIn',
        component: SignIn
    },
    {
        path: '/signUp',
        component: SignUp
    },
    {
        path: '/forgot_password',
        component: Forgot_Passwort
    },
    {
        path: '/home',
        component: Home
    }
]


